$cell-size: 100px;
$gap: 1px;
$padding: 10px;
$calendar-size:
  $cell-size * 7 +
  $gap * 6 +
  $padding * 2
;
$month-length: 29;
$start-day: $cell-size + $gap;

$week:
  "mon" 0,
  "tue" $start-day,
  "wen" $start-day * 2,
  "thu" $start-day * 3,
  "fri" $start-day * 4,
  "sat" $start-day * 5,
  "sun" $start-day * 6;
